import React from 'react';
import Porptype from 'prop-types';
import "./Movie.css";

function Movie({year, title, summary, poster, genres}) {
return (
    <div className = "movie">
        <img src = { poster } alt = { title } title = { title }/>
        <div className = "movie_data">
            <h3 className = "movie_title">{ title }</h3>
            <h5 className = "movie_year">{ year }</h5>
            <ul className = "genres">
                { genres.map((genre, index) => (
                    <li key = { index } className = "genres_genre">
                        { genre }
                    </li>
                ))}
            </ul> 
            <p className = "movie_summary">{ summary.slice(0, 180) }...</p>
        </div>
    </div>
);
}

Movie.prototype = {
    id: Porptype.number.isRequired,
    year: Porptype.number.isRequired,
    title: Porptype.string.isRequired,
    summary: Porptype.string.isRequired,
    poster: Porptype.string.isRequired,
    genres: Porptype.arrayOf(Porptype.string).isRequired
}

export default Movie;